import { getAllConcepts } from '../components/Firebase'
import Link from 'next/link'
import AppContainer from '../components/AppContainer'

const Index = props => (
  <AppContainer>
    <h1>A list of all the concepts</h1>
    <ul>
      { console.log('hello from index.js here are the props', props) }
      {props.concepts.map(concept => (
        <li key={concept.id}>
          <Link href="/explainthis/[id]" as={`/explainthis/${concept.id}`}>
            <a>{concept.concept}</a>
          </Link>
        </li>
      ))}
    </ul>
  </AppContainer>
);

Index.getInitialProps = async function() {
  console.log('i am here!!!'); // this is never printed

  const data = await getAllConcepts();

  console.log(`Show data fetched: ${data}`); // neither is this

  return {
    concepts: data.map(entry => entry.show)
  };
};

export default Index;
